import React from "react";

import CustomLink from "../CustomLink/CustomLink.component";

import { LinksListContainer } from "./LinksList.styles";

import data from "./LinksList.data.js";

const LinksList = () => (
	<LinksListContainer>
		<ol>
			{data.map(({ id, ...fields }) => (
				<li key={id}>
					<CustomLink {...fields} />
				</li>
			))}
		</ol>
	</LinksListContainer>
);

export default LinksList;
