import React from "react";

import {
	CustomLinkStyled,
	CustomLinkContainer,
	CustomLinkImageBox,
} from "./CustomLink.styles.jsx";

const CustomLink = ({ link, title, icon }) => (
	<CustomLinkContainer>
		<CustomLinkStyled href={link} target="__blank">
			<CustomLinkImageBox>
				<img src={icon} alt="" />
			</CustomLinkImageBox>
			{title}
		</CustomLinkStyled>
	</CustomLinkContainer>
);

export default CustomLink;
